export default {
	data() {
		return {
			height: '',
			height_a: '',
			KG: false,
			KG_sc: false,
			state: ["在售中", "已售空", "已下架"],
			allCategories: [],
			commodities: [],
			id: '',
			dow: '../../static/x.png',
			up: '../../static/s.png',
			key:-1,
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.getSort();
			}
		});
	},
	onReady() {
		this.getTopheight();
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
					that.height_a = res.statusBarHeight + 44 + 50 + "px";
				}
			})
		},
		open: function() {
			this.KG = !this.KG;
			this.KG_sc = false;
		},
		open_screen: function() {
			this.KG_sc = !this.KG_sc;
			this.KG = false;
		},
		getSort: function() {
			let that = this;
			uni.request({
				url: this.serveipd + '/api/merchantApi/allCategories',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
				},
				success: res => {
					for (let i = 0; i < res.data.data.length; i++) {
						that.allCategories.push(res.data.data[i])
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getCommodity: function(category_id) {
			this.KG = !this.KG;
			let that = this;
			that.id = category_id;
			uni.request({
				url: this.serveipd + "/api/merchantApi/classified",
				method: "GET",
				data: {
					merchantId: that.merchantId,
					category_id: category_id
				},
				success(res) {
					console.log(res);
					let list = [];
					for (let i = 0; i < res.data.data.length; i++) {
						list.push(res.data.data[i]);
					}
					that.commodities = list;
				}
			})
		},
		screen: function(commodityStatus) {
			this.KG_sc = !this.KG_sc;
			let that = this;
			uni.request({
				url: this.serveipd + "/api/merchantApi/screen",
				method: "GET",
				data: {
					merchantId: that.merchantId,
					commodityStatus: commodityStatus,
					category_id: that.id
				},
				success(res) {
					console.log(res);
					let list = [];
					for (let i = 0; i < res.data.data.datas.length; i++) {
						list.push(res.data.data.datas[i]);
					}
					that.commodities = list;
				}
			});
		},
		// 下架
		cease: function(id, index) {
			let that = this;
			uni.request({
				url: this.serveipd + "/api/merchantApi/lowerShelf",
				method: "GET",
				data: {
					id: id,
					enabled: 0,
				},
				success(res) {
					let result=that.commodities.find(item=>item.id==id);
					result.enabled =0;
					console.log(res);
				}
			})
		},
	}
}
